@import '../variables.scss';


.ui-background{
  display: flex;
  > span > div{
    width: $w;
    background: white;
    margin-left:8px;
    cursor: pointer;
    border-radius: 2px;
    border: 1px solid $dd;
    overflow: hidden;
    &:hover{
      border-color: $steel;
    }
  }

  .bg-gradient {
    margin:0 8px;
  }

  .bg-image{
    width: $w;
    height: $w - 3;
    background-size: cover;
    background-repeat: repeat;
    background-position: center;
    border: 1px solid $dd;
    cursor: pointer;
    border-radius: 2px;
    position: relative;
    background-color: white;
    margin-right:8px;
    &:hover{
      border-color: color($dd shade(10%));
    }
    &::before{
      content: "\F030";
      font-size: 19px;
      color: $dd;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      top: 1px;
    }
    &.has-image{
      &::before{
        display: none;
      }
    }
  }
}


.has-gradient:hover{
  .gradient-preview::before{
      color: white;
  }
}

.bg-image-popover{
  .bg-image-preview{
    img{
      max-width: 200px;
      height: auto;
      max-height: 200px;
    }
  }
}

.bg-popover{
  .form-field{
    flex-grow: 0;
  }
  .input-numbers{
    margin-right:8px;
  }
}

a.grad {
  position: absolute;
  right: 75px;
  top: 88px;
  display: inline-block;
  padding: 0 3px;
  border-radius: 2px;
  transition: all 0.15s ease-out;
  font-size: 11px;
  z-index: 2;
  text-decoration: underline;
  + a{
    right: 10px;
  }
  + .ui-text{
    textarea{
      height: 100px;
    }
    + .variables{
      margin-top: 0;
      max-width: 100%;
      &::before{
        text-align: left;
        padding-left: 5px;
      }
      > div > div {
        width: calc(100% / 9);
        height: 36px;
      }
      .gradient-preview {
        width: 100%;
        border-radius: 0;
      }
    }
  }
}

.ui-gradient {
  .ui-clear.ui-edit{
    opacity: 0;
  }
  .bg-gradient{
    width: $w;
    height: $w - 3;
    position: relative;
    &:hover{
      .ui-clear.ui-edit{
        opacity: 1;
      }
    }
  }
}

.gradient-preview{
  width: 33px;
  height: 30px;
  position: absolute;
  border-radius: 2px;
  top: 0;
  left: 0;
  cursor: pointer;
  background: white;
  border: 1px solid $dd;
  &::before{
    content: "G";
    font-family: $sans;
    font-weight:700;
    font-size: 21px;
    color: $dd;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
  }
  &:hover{
    background: $alice;
  }
}

.ui-icon-editor,
.ui-background {
  > .ui-checkbox{
    margin-left: 8px;
    label{
      min-width: 48px;
      border-radius: 2px !important;
      &.is-checked{
        background: $orange !important;
      }
    }
  }
}

.nobg .ui-background > *:nth-child(n+3){
  display: none;
}
