@import '../variables.scss';

$bg: $alice;

$mw: 46px;
$h: 29px;

.ui-input,
.ant-select-auto-complete.ant-select {
  position: relative;
  margin-right: 8px;
  display: inline-block;
  input,
  .ant-input {
    line-height: 28px;
    padding: 0 8px;
    background: white;
    transition: all 0.1s ease-out;
    border-radius: 2px;
    border: 1px solid $dd;
    font-size: 12px;
    font-family: $sans;
    &:hover {
      background: $bg;
      border-color: $dd;
    }
    &:focus {
      background: $bg;
      outline: 0;
      border-color: $lightblue;
      box-shadow: none;
    }
  }
  &.input-large {
    input, textarea {
      width: 250px;
    }
  }
  &.input-number {
    input {
      width: $w !important;
      height: $h;
      padding: 0 3px;
      text-align: center;
    }
  }
  .ant-input {
    width: $mw;
  }
  &.input-numbers {
    input {
      width: $mw;
      height: $h;
      padding: 0 5px;
      text-align: center;
    }
    &:not(:first-of-type)::before {
      //content: "-";
      margin: 0 2px;
    }
  }
  &.labeled {
    margin-top: 7px;
  }
  span:not(:first-of-type) {
    margin-top: 7px;
  }
  input + img {
    margin-left: 7px;
  }
}

.input-full{
  .input-large{
    width: 100%;
    input{
      width: 100%;
    }
  }
}

.row-options .input-large,
.row-options .input-large input {
  width: 100%;
  margin:0;
}

.ui-input.ui-input-number {
  input{
    min-height: 29px;
    //max-width: 52px;
    text-align: center;
  }
}

.input-number + .radio-number,
.input-number + .ui-color,
.input-number + .edit-btn,
.ui-multi + .ui-color {
  //margin-left: 8px;
}

.ui-input-lang {
  width: 100%;
  > div {
    display: flex;
    align-items: center;
    background: $e4;
    //max-width: 400px;
    + div{
      margin-top: 5px;
    }
  }
  .ui-input {
    margin-right: 0;
    width: 100%;
    input{
      width: 100% !important;
    }
  }
}

.ui-text-lang {
  width: auto;
  margin: -10px;
  flex-grow: 1;
  > div{
    align-items: flex-start;
  }
  .tab-container{
    width: 100%;
    //padding-bottom: 10px;
    //padding-right: 10px; interferes with block module content type Code
  }
  .ui-text textarea{
    width: 100%;
    height: 150px;
  }
}

.lang-flag {
  display: inline-block;
  width: 30px;
  height: 30px;
  position: relative;
  img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.ui-input:not(:first-of-type) {
  .input-label {
    margin-left: 7px;
  }
}

.input-label {
  display: block;
  position: absolute;
  width: 100%;
  text-align: center;
  top: -14px;
  opacity: .6;
  font-style: normal;
  font-size: 10px;
}

.ui-text {
  textarea {
    display: flex;
    padding: 5px 8px;
    width: 350px;
    height: 120px;
    line-height: 1.5;
    background: white;
    transition: all 0.1s ease-out;
    border-radius: 2px;
    border: 1px solid $dd;
    font-size: 12px;
    font-family: $sans;
    &:hover {
      background: $bg;
    }
    &:focus {
      background: $bg;
      outline: 0;
      border-color: $lightblue;
    }
  }
}

$pl: color($steel tint(40%));

::-webkit-input-placeholder {
  color: $pl;
  font-size: 11px;
}

::-moz-placeholder {
  color: $pl;
  font-size: 11px;
}

:-ms-input-placeholder {
  color: $pl;
  font-size: 11px;
}

/******************************
 inputValue
*******************************/

.ui-input-value,
.ant-select-auto-complete.ant-select .ant-input {
  height: $h;
  .ant-input {
    text-align: center;
  }
}

.ant-input:not(.ant-calendar-picker-input) {
  max-width: $mw;
}

.ui-input {
  .ant-input-number,
  .ant-select-focused .ant-select-selection,
  .ant-select-selection,
  .ant-select-selection:focus {
    border: 0;
    outline: 0;
    box-shadow: none;
  }
}

.ant-select-dropdown {
  width: auto !important;
  min-width: $mw;
  background: white;
  border-radius: 2px;
  border: 0;
  box-shadow: 0 5px 30px rgba(0, 0, 0, 0.2);
}

.ant-select-dropdown-menu-item {
  padding: 8px;
}

.ant-select-auto-complete.ant-select {
  margin-right: 0;
}

.ant-select-search.ant-select-search--inline {
  margin-top: 0;
}

/******************************
 inputNumber
*******************************/

.ant-input-number {
  width: auto;
  height: auto;
  background: transparent;
}

.ant-input-number-handler-up-inner,
.ant-input-number-handler-down-inner {
  transform: none;
  &::before {
    content: "\f106";
    font-family: FontAwesome !important;
    font-size: 12px;
  }
}

.ant-input-number-handler-down-inner:before {
  content: "\f107";
}

.ant-input-number-handler-down {
  top: 0;
}

.ant-input-number-handler-up,
.ant-input-number-handler-down {
  display: flex;
  align-items: center;
  justify-content: center;
  &:hover {
    height: 50% !important;
    background: $blue2;
    .ant-input-number-handler-up-inner,
    .ant-input-number-handler-down-inner {
      color: white;
    }
  }
  &.ant-input-number-handler-up-disabled,
  &.ant-input-number-handler-down-disabled {
    &:hover {
      background: color($orange tint(80%));
    }
  }
}

.ant-input-number-handler-wrap:hover .ant-input-number-handler {
  height: 50%;
}

.ant-input-number-handler-up-inner,
.ant-input-number-handler-down-inner {
  top: auto;
  margin-top: 0;
}

.ui-input span:not(:first-of-type),
.ant-select-auto-complete.ant-select span:not(:first-of-type) {
  margin-top: 0;
}

.ant-input-number-handler-wrap {
  opacity: 1;
  border: 1px solid $dd;
  border-radius: 0;
  border-top-right-radius: 2px;
  border-bottom-right-radius: 2px;
  height: $h;
  background: $f7;
}

.ant-input-number-input-wrap {
  padding-right: 21px;
}

.ant-input-number-focused {
  .ant-input-number-handler-wrap {
    border-color: $lightblue;
  }
}

.ant-select-selection{
  background-color: transparent !important;
}


.breakpoints .ui-input .ant-input{
  width: 50px;
  max-width: 50px;
  padding:0 5px;
}

.ui-margin input{
  text-transform: uppercase;
}

.ant-input-number-handler-up-inner svg, .ant-input-number-handler-down-inner svg {
  width: 9px;
  left: 50%;
  position: relative;
  transform: translateX(-50%);
}



