@import '../variables.scss';

.ui-multi,
.ui-text-shadow,
.ui-border-radius,
.ui-shadow,
.ui-border {
  display: flex;
}

.ui-color + .ui-radio {
  margin-left: 8px;
}

.ui-shadow{
  .ui-toggle{
    display: flex;
    align-items: center;
  }
  .ui-checkbox{
    margin-left:8px;
    label{
      background-color: $quartz;
      min-width: 45px;
    }
  }
  .shadow-preview{
    position: relative;
    &::before{
      content: "S";
    }
  }
}

.ui-border,
.ui-divider{
  .ui-select{
    margin-left: 8px;
    .Select{
      width: 95px;
    }
  }
}

.ui-divider{
  .ui-radio{
    > span {
      label > span{
        transform: rotate(90deg);
      }
      &:nth-child(2) label > span{
        transform: rotate(0deg);
        font-weight: bold;
        font-size: 14px;
        position: relative;
        top: -1px;
      }
    }
    > span:nth-of-type(3) label > span{
      font-size: 10px;
    }
    > span:first-of-type label > i{
      opacity: 1;
      &::before{
        position: relative;
        top: -1px;
        font-size: 16px;
      }
    }
  }
}


.border-popover{
  .form-field{
    .ui-input{
      margin-right:8px;
    }
    .ui-radio{
      .icon{
        &::before{
          position: relative;
          font-size: 15px;
        }
      }
      .fa-ban{
        font-size: 18px;
        position: relative;
        top: 1px;
      }
      label{
        min-height: 29px;
        min-width: 32px !important;
      }
      &:not(.radio-number){
        label{
          font-size: 8px !important;
        }
      }
      > span {
        label > span{
          transform: rotate(90deg);
        }
        &:nth-child(2) label > span{
          transform: rotate(0deg);
          font-weight: bold;
          font-size: 14px;
          position: relative;
          top: -1px;
        }
      }
      > span:nth-of-type(3) label > span{
        font-size: 9px;
      }
      > span:first-of-type label > i{
        opacity: 1;
        &::before{
          position: relative;
          top: -1px;
          font-size: 16px;
        }
      }
    }
    &:nth-of-type(3){
      position: absolute;
      top: 54px;
      left: 55px;
    }
    .ui-radio.radio-number{
      margin-left:0;
    }
    &:nth-child(3){
      .ui-radio{
        label {
          min-width: 32px;
          text-transform: none;
          font-size: 11px;
          min-height: 29px;
        }
        > span:nth-of-type(2),
        > span:nth-of-type(3),
        > span:nth-of-type(4){
          label{
            min-width: 54px;
          }
        }
      }
    }
  }
}

.ui-border-radius {
  a{
    margin-left: 8px;
  }
  .mini-select .Select{
    width: 33px !important;
    margin-right: 8px;
  }
  .select-edit-value{
    justify-content: center;
  }
  .mini-select .select-edit-value .select-edit-label{
    padding: 0;
  }
  .mini-select :not(.Select--multi) > .Select-control .Select-value{
    padding: 0;
    text-align: center;
  }
  .mini-select :not(.Select--multi) > .Select-control .Select-value{
    overflow: visible;
  }
  .mini-select .select-edit-value .select-edit-button a{
    top: 0;
    transform: translate(50%, -50%);
  }
  .ui-value .Select:not(.has-value) .Select-placeholder {
    white-space: normal;
    text-overflow: initial;
    left: -2px;
    font-size: 11px;
    color: #b3bac1 !important;
    padding-right: 0;
  }
}

.right-left{
  .ui-input{
    &:nth-child(1),
    &:nth-child(2),
    &:nth-child(4){
     display: none;
    }
    &:nth-child(3){
      order:2;
    }
  }
}

