@import "variables";

.page, .modal, .ant-popover, .ant-modal {
  ::before {
    font-family: 'icomoon';
    font-weight: normal;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
}

.note-editor ::before {
  font-family: summernote !important;
}

#container {
  background: $fa;
  display: flex;
  flex-direction: column;
}

#content {
  padding-bottom: 0;
  flex-grow: 1;
  transition: all 0.3s;
}

div[data-name="logo"],
div[data-name="logo2x"]{
  .ui-image-preview {
    border: 1px dashed #d0d6de;
    padding: 10px;
  }
}

.active + #content {
  //transition: margin 0.3s;
}

#footer {
  background: white;
  padding-top: 34px;
  margin: auto 0 0 0;
  padding-left: 50px;
  border-top: 1px solid $ee;
  transition: all 0.3s;
}

.field-label small a{
  color:$blue !important;
  text-decoration: underline;
  &:hover{
    color: $orange !important;
  }
}

.active ~ #footer {
  padding-left: 250px;
}

#column-left {
  &.active {
    //overflow-y: scroll;
  }
  + #content {
    + #footer {
      margin-left: 50px;
    }
  }
}

.page-layout {
  background-color: $e2;
}

.j-content {
  //min-width:870px;
}

.page-modules.page-footer {
  .j-content {
    padding: 8px;
  }
}

.page-module .j-content > div > div:not([class]) {
  padding: 8px;
}

@media only screen and (max-width: 1024px) {
  .j-content {
    padding: 0;
  }
  .field-label {
    max-width: 150px;
  }
  .tab-container {
    border-left: 0;
  }
}

.ui-clear {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 11px;
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(50%, -50%) scale(.8);
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, .4);
  background: $orange;
  color: white;
  border-radius: 50%;
  width: 15px;
  height: 15px;
  z-index: 1;
  &:hover {
    color: white;
    background: $night;
    transform: translate(50%, -50%) scale(.9);
  }
  &::before {
    //content: "\f00d";
  }
  &.ui-edit {
    left: 0;
    transform: translate(-50%, -50%) scale(0.8);
  }
}

.image-dimensions > .form-field > .field-option {
  > div {
    width: 100%;
  }
}

.note {
  display: block;
  padding: 10px 15px;
  line-height: 1.7;
  b {
    font-weight: 700;
  }
}

/******************************
 Variables
*******************************/

.page-variables {
  .ui-input:not(.input-numbers, .input-number) input {
    width: 188px;
  }
  .input-numbers {
    &::before {
      display: none;
    }
  }
}

.page-not-found .j-content,
.page-dashboard .j-content {
  h1 {
    text-align: center;
    margin: 20px;
  }
}

.field-variable-name + .form-field{
  > .field-option{
    border-left: 1px solid #eee;
  }
  .items-per-row-section{
    margin:-10px;
    .list-add-btn {
      height: calc(100% - 20px);
      right: -28px;
      transition: all 0s !important;
    }
    .ui-list-group + div > ul:empty + .list-add-btn {
      right: 10px;
    }
    .ui-list-group{
      min-width: 160px;
      + div .ui-list-group{
        min-width: 123px;
      }
    }
  }
}

/******************************
 Save toast
*******************************/

.ant-message {
  z-index: 999999;
  top: 12px;
  .oc3 & {
    top: 19px;
  }
  svg, i{
    display: none;
  }
}

.ant-message-notice {
  padding: 3px;
}

.ant-message-notice-content {
  background: #27ae5f;
  color: white;
  border-radius: 3px;
  padding: 5px 10px;
  box-shadow: 0 3px 30px 0 rgba(0, 0, 0, .2);
  > div {
    display: inline-flex;
    align-items: center;
    justify-content: center;
  }
}

.ant-message .anticon {
  color: white;
  font-size: 17px;
  margin-right: 3px;
  top: 0;
  &::before {
    content: "\eb1b";
    font-family: 'icomoon' !important;
  }
}

.fa-spin-hover:hover {
  animation: fa-spin 2s infinite linear;
}

.fa-spin-hover-parent:hover .fa {
  animation: fa-spin 2s infinite linear;
}

.ant-alert-info {
  border: none;
  padding: 0 !important;
  background: transparent !important;
}

.navbar-static-top {
  .green {
    margin-right: 5px;
  }
}

.ui-font {
  .VirtualizedSelectOption {
    display: flex;
    align-items: center;
    > div {
      display: block;
      width: 100%;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
}

.page-variables {
  .module-items > div:not(.tab-container) > a {
    padding-left: 12px;
  }
}

.modal-backdrop{
  z-index:999;
  pointer-events: none;
}

.variable-radius,
.variable-font,
.variable-value,
.variable-breakpoint,
.variable-color,
.variable-gradient,
.variable-shadow,
.variable-font_size {
  display: flex !important;
  align-items: center;
  justify-content: center;
  .variable-name {
    order: 2;
    margin-left: 5px;
  }
  .variable-value {
    order: 0;
    color: #C1D0DF;
    &::after {
      content: "";
      margin-left: 5px;
    }
  }
}


.variable-font {
  .variable-value {
    order: 3;
    min-width: 25px;
    &::after {
      display: none;
    }
    &::before {
      content: "";
      margin-right: 5px;
      margin-left: 5px;
    }
  }
}

.j-content{
  .pagination {
    padding: 8px;
    > ul{
      margin-bottom: 5px;
    }
    > span{
      font-size: 12px;
      opacity: .8;
    }
  }
}

.variable-font_size {
  .variable-value {
    min-width: 26px;
  }
}

.variable-color,
.variable-shadow,
.variable-gradient {
  margin-left: -4px;
  .variable-value {
    height: 25px !important;
    width: 25px !important;
    margin-right: 5px;
    border: 1px solid #f4f4f4;
    &::after {
      display: none;
    }
  }
}

.variable-gradient {
  .variable-value span {
    width: 100%;
    height: 100%;
  }
}

.module-items-empty {
  .ant-alert-message {
    padding: 0 10px;
  }
  + .pagination {
    display: none;
  }
}

.import-buttons,
.export-buttons {
  .button {
    margin-right: 6px;
  }
}

.ui-none > *:not(:last-child) {
  opacity: .25;
}

.image-tools {
  display: flex;
  > div {
    margin-left: 20px;
  }
}

/* @todo */
.page-module_header,
.page-footer {
  li.flyout_menu {
    display: none;
  }
}

.database-indexes-modal .ant-modal-body {
  padding: 10px !important;
  max-height: 500px;
  overflow-y: auto;
}

.add-indexes-btn {
  background-color: $green !important;
  margin-right: 18px;
}

.check-indexes-btn {
  background-color: $green !important;
}

.php-ini {
  display: flex;
  > div {
    margin-right: 15px;
  }
}

.blog-views,
.blog-comments{
  color: #A5AFB9;
  display: inline-flex;
  padding: 0 5px;
  align-items: center;
  opacity: .7;
  i{
    margin-right: 5px;
  }
}
.blog-comments{
  margin-right: 10px;
}
.comment-status{
  color: #A5AFB9;
  margin-right: 10px;
  .fa-toggle-on{
    color: $green;
  }
  .fa-toggle-off{
    color: $orange;
  }
}

html[dir='rtl'],
html[lang='ar'] {
  .j-header,
  .j-content,
  .ant-popover,
  .ant-modal {
    direction: ltr;
  }

  &.oc2 {
    .list-add-btn,
    .list-remove-btn {
      right: 50px;
    }
  }
}


// Hide download button from some pages
.page-layouts,
.page-blog_categories,
.page-blog_posts {
  .module-items .item-download {
    display: none;
  }
}

.ui-display.sub-tabs{
  width: 100%;
  width: stretch;
  margin: -10px;
}


.j-header > .j-button{
  position: relative;
  cursor: pointer;
  left: 23px;
  .button{
    margin:0 3px;
    &:hover{
      background: $blue;
    }
  }
}

.license-check-btn {
  width: 30px;
  height: 27px;
  display: flex;
  justify-content: center;
}

.license-check-modal {
  .module-items {
    color: #fff;
    > div {
      padding: 0 10px;
    }
  }
}
