@import '../variables.scss';

$hov: $lightblue;

.sketch-picker {
  padding: 0 !important;
  box-shadow: none !important;
  background: transparent !important;
  margin-bottom: -10px;
  input {
    text-align: center;
    width: 30px !important;
    &:focus {
      box-shadow: none;
      outline: 1px solid $lightblue;
    }
    + span{
      font-size: 9px !important;
      text-transform: uppercase !important;
      padding-top: 1px !important;
    }
  }
  > div:nth-of-type(3) {
    > div:first-of-type {
      input {
        width: 60px !important;
      }
    }
  }
  > div:nth-last-of-type {
    border-bottom: 1px solid $f4;
  }
  > div:last-of-type {
    border-top: 0 !important;
    padding-top: 27px !important;
    display: none !important;
  }
}

.ui-color {
  position: relative;
  width: $w;
  height: $w - 3px;

  .ui-color-swatch {
    position: relative;
    z-index: 1;
    border-radius: 2px;
    border: 1px solid $dd;
    color: white;
    width: 100%;
    height: 100%;
    cursor: pointer;
    transition: all 0.05s ease-out;
    //background: currentColor;
    background: white;
    &::before {
      display: flex;
      align-items: center;
      justify-content: center;
      content: "\e910";
      font-size:19px;
      color: color($dd shade(5%));
    }
    &:hover {
      background: $alice;
      &::before {
        color: color($lightblue shade(0%));
      }
    }
    &.has-color {
      //border-color: transparent;
      &::before {
        color: inherit;
        content: "\e918";
      }
      &:hover {
        //background: currentColor;
      }
    }
  }
  .ui-color-popover {
    position: relative;
    z-index: 2;
  }
  .ui-color-cover {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }
  &::after {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    z-index: 0;
    background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAQCAYAAAAf8/9hAAAAMElEQVQ4T2N89uzZfwY8QFJSEp80A+OoAcMiDP7//483HTx//hx/Ohg1gIFx6IcBALl+VXknOCvFAAAAAElFTkSuQmCC);
  }
}

.ui-color .ui-color-swatch.has-color[style="color: rgb(255, 255, 255);"] {
  border-color: $dd;
  &::before {
    text-shadow: 0 0 1px rgba(0, 0, 0, 0.5);
  }
}

.ui-color .ui-color-swatch.has-color[style="color: rgba(255, 255, 255, 0);"] {
  background: currentColor;
}

.ui-color .ui-color-swatch.has-color[style="color: rgba(0, 0, 0, 0);"] {
  background: currentColor;
}

.picker-backdrop {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
}

.ui-color-popover {
  z-index: 2999;
}

.text-shadow .ui-color-popover {
  min-width: 223px;
  .variables {
    min-width: 100%;
  }
}


.quick-edit-var{
  align-items: center;
  justify-content: center;
  font-size: 11px;
  position: absolute;
  top: 0;
  right: 0;
  transform: translate3d(100%,0,0);
  -webkit-backface-visibility: hidden;
  background: $orange;
  color: white;
  border-radius: 0;
  width: 20px;
  height: 20px;
  z-index: 1;
  display: none;
  box-shadow: 10px 5px 50px 0 rgba(0,0,0,.7);
  i{
    transform: translate3d(0,0,0);
    -webkit-backface-visibility: hidden;
  }
  &:hover{
    background: $green;
    color: white;
  }
}

//body > div:last-of-type{
//  z-index: 9999;
//  position: relative;
//}

.ant-popover-placement-top > .ant-popover-content > .ant-popover-arrow{
  background: transparent;
  box-shadow: none;
}

.variables {
  order:2;
  position: relative;
  padding-top: 7px;

  > div{
    position: relative;
    display: flex;
    flex-wrap: wrap;
    margin-right:-6px;
    margin-bottom:-6px;
    -webkit-backface-visibility: hidden;
    > div {
      color: transparent;
      flex: 0 0 auto;
      width: 20%;
      height: 25px;
      position: relative;
      border-width: 0 6px 6px 0;
      border-color: #F6F8F9;
      border-style: solid;
      background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAQCAYAAAAf8/9hAAAAMElEQVQ4T2N89uzZfwY8QFJSEp80A+OoAcMiDP7//483HTx//hx/Ohg1gIFx6IcBALl+VXknOCvFAAAAAElFTkSuQmCC);
      &.selected {
        span {
          outline: 1px solid red;
          border-width: 0;
        }
      }
      > span {
        border: 1px solid $gray;
        display: block;
        size:100%;
        cursor: pointer;
        background-color: currentColor;
        &:hover{
          border-color: red;
        }
      }
    }
  }

  > div > div{
    width: calc(100% / 7);
    height: 26px;
    &:hover{
      .quick-edit-var{
        display: flex;
      }
    }
  }
}

.ui-color-popover{
  .ant-popover-inner-content{
    > div{
      display:flex;
      flex-direction:column;
    }
    .sketch-picker{
      order:1
    }
    .color-fields{
      border-top: 1px solid #D6D9DE;
      order: 3;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      margin-top: 8px;
      padding-top: 8px;
      position: relative;
      //display: none;
      &::before{
        content: "Lighten / Darken Variable";
        //text-transform: uppercase;
        font-family: $sans;
        font-size: 10px;
        position: absolute;
        top: 4px;
        left: 0;
        color: #333;
      }
      &::after{
        content: "-10 (ligher) / 10 (darker)";
        //font-style: italic;
        font-family: $sans;
        font-size: 10px;
        position: absolute;
        top: 18px;
        left: 0;
        opacity: .8;
      }
      input{
        max-width: 50px;
        margin-right: 2px;
        padding: 3px 5px;
        border: none;
        text-align: center;
        box-shadow: rgb(204, 204, 204) 0px 0px 0px 1px inset;
        font-size: 11px;
        &:focus{
          outline: 1px solid #B2DBF4;
        }
        &::-webkit-inner-spin-button,
        &::-webkit-outer-spin-button{
          opacity: 1;
        }
      }
    }
    .variables{
      order:2;
      position: relative;
      max-width: 206px;
      border-top: 1px solid #D6D9DE;
      margin-top: 10px;
      padding-top: 22px;
      &::before {
        content: "Color Variables";
        font-family: $sans;
        text-transform: uppercase;
        font-size: 9px;
        position: absolute;
        top: 3px;
        width: 100%;
      }
    }
  }
}
