@import './variables.scss';

$pad2: 30px;
$bg: $blue;
$h: 40px;
$col: color($dark tint(6%));
$bs: solid;
$bc: $bor;

$hover: $blue;

/******************************
 Position Selector Buttons
*******************************/

.page-layout {
  .j-content {
    min-width: 970px;
  }
}

.active-pos {
  float: left;
  min-width: $lw;
  text-align: center;
  color: white;
  opacity: .5;
  background: $night;
  min-height: $h;
  line-height: 40px;
  font-size: 13px;
  font-weight: 500;
  letter-spacing: .2px;
  font-family: $mont;
  text-transform: uppercase;
  &::after {
    //content: "\e5c8";
    position: relative;
    top: 1px;
    right: -7px;
    font-family: icomoon;
  }
}

.position-selectors {
  background: $asphalt;
  position: fixed;
  z-index: 9;
  top: 90px;
  min-height: $h;
  width: calc(100% - 235px);
  box-shadow: 0 1px 10px rgba(0, 0, 0, .4);
  transition: width 0.3s;
  display: flex;
  align-items: center;
  > div{
    margin-left: 8px;
  }
  .active-pos{
    margin: 0;
  }
  .ui-checkbox {
    margin-right: auto;
    margin-left: 0;
    max-width: 900px;
    padding: 0;
    display: flex;
    flex-flow: row nowrap;
    > span {
      margin-right: 0;
      border: 0;
      &:last-of-type {
        label {
          border-right: 1px solid #4c5156;
        }
      }
      &:last-of-type .is-checked {
        border-right-color: transparent;
      }
      &:first-of-type .is-checked {
        border-left: 0;
      }
    }
    label {
      color: $steel;
      background: $col;
      padding: 0;
      border-radius: 0 !important;
      min-height: $h;
      line-height: $h;
      min-width: 45px;
      font-family: $mont;
      font-weight: 700;
      border-left: 1px solid color($col shade(30%));
      border-right: 1px solid color($col tint(10%));
      font-size: 13px;
      span {
        width: 100%;
        height: 100%;
        display: block;
      }
      &:hover {
        background: color($midnight shade(10%));
      }
      &::after {
        content: "";
        background: transparent;
        min-width: 45px;
        height: 4px;
        position: absolute;
        bottom: 0;
        left: 0;
      }
    }
    .is-checked {
      border-left: 1px solid color(#2A2E40 shade(5%));
      border-right: 1px solid color(#535A71 shade(10%));
      background: color($midnight tint(5%)) !important;
      color: rgba(#B2DBF4, .8);
      &::after {
        background: $bg;
      }
    }
  }
  .oc3 & {
    top: 107px;
  }
}

.on-off {
  position: absolute;
  right: 0;
  z-index: 1000;
  //display: none;
}

#column-left.active + #content {
  .position-selectors {
    width: calc(100% - 235px);
  }
}

.styles-override,
.absolute-modules,
.global-modules {
  a {
    background: $marine;
    i {
      animation: none !important;
      &::before {
        position: relative;
        font-size: 22px;
        top: -1px;
        opacity: .7;
      }
    }
    &:hover i::before {
      opacity: 1;
    }
  }
}

.styles-override {
  margin-right: 8px;
  order: 100;
  a i::before {
    font-size: 18px;
    content: "\eb50";
  }
}

.absolute-modules {
  order: 99;
  a i::before {
    font-size: 18px;
    content: "\e923";
  }
}

.global-modules {
  order: 98;
  a i::before {
    font-size: 18px;
    content: "\e916";
  }
}

.modal-styles-override .ant-modal {
  max-width: 438px;
  .ui-select .Select {
    width: 200px;
  }
}

/******************************
 Header / Footer / Page Content
*******************************/

.page-layout {
  .header,
  .footer,
  .default-content {
    border-radius: 3px;
    overflow: hidden;
    background: $asphalt;
    min-height: 70px;
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    .position-heading {
      position: absolute;
      background: transparent;
      color: $e4;
      display: none;
    }
    &:before {
      font-family: $mont;
      width: 100%;
      text-align: center;
      content: attr(data-name);
      text-transform: uppercase;
      font-size: 27px;
      font-weight: 700;
      //transition: all 0.1s;
      color: #586773;
    }
  }
}

.header, .footer {
  margin-bottom: $pad2;
}

.default-content {
  min-height: 115px;
}

/******************************
 Positions Container
*******************************/

.page-layout {
  .j-content {
    padding: 0;
  }
  .positions {
    padding: $pad2 - 5 $pad2 - 10 0 $pad2 - 5;
    display: flex;
    flex-direction: column;
    position: relative;
    min-height: 500px;
    margin-top: 130px;
    background: $e2;
    a:active {
      box-shadow: none;
    }
  }

  .nested-positions {
    display: flex;
    flex-grow: 1;
    margin-bottom: $pad2;
    background: transparent;
    min-height: 354px;
  }

  .center-content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    min-height: 150px;
  }
}

.oc3 .page-layout .positions {
  margin-top: 40px;
}

/******************************
 Position Buttons
*******************************/

$rad: 2px;

.position > .position-heading {
  height: 0;
  > span {
    cursor: default;
    background: $blue;
    color: white;
    line-height: 100%;
    height: 20px;
    padding-left: 8px;
    border-top-left-radius: $rad;
    border-top-right-radius: $rad;
    font-size: 10px;
    //z-index: 5;
    position: relative;
    display: flex;
    align-items: center;
    top: -10px;
  }
}

a.add-row {
  width: 20px;
  height: 20px;
  color: white;
  background: $teal;
  cursor: pointer;
  //z-index: 5;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 7px;
  border-top-right-radius: $rad;
  //border-bottom-right-radius: $rad;
  > a {
    font-size: 12px;
    padding: 0;
    position: relative;
    //padding-right:1px;
    color: inherit;
    top: 1px;
  }
  &:hover {
    background: $green3;
  }
}

.button-set {
  position: absolute;
  right: 12px;
  top: 5px;
  color: $steel;
  //background: $e6;
  .button {
    color: inherit;
    padding: 5px;
    font-size: 13px;
    display: flex;
    align-items: center;
    justify-content: center;
    &:hover {
      color: $hover;
    }
  }
  .edit-column {
    display: block;
    position: absolute;
  }
}

.buttons-dropdown {
  padding: 3px;
  display: flex;
  flex-flow: column nowrap;
  background: $e7;
  z-index: 20;
  box-shadow: 0 10px 30px 0 rgba(0, 0, 0, .2);
  margin-top: 20px;
  //display: none;
}

.move-handle {
  background: white;
  width: 15px;
  height: 24px;
  border-right: 1px solid $bor;
  z-index: 5;
  //transition: all 0.1s ease-out;
  &:hover {
    color: white;
    background: $hover;
  }
}

.column-wrapper .move-handle {
  height: 100%;
}

.module-wrapper {
  .move-handle {
    border: 1px solid $bor;
  }
}

/******************************
 Position - General
*******************************/

.position-heading {
  color: $dark;
  position: relative;
  height: $m25;
  width: 100%;
  font-size: 13px;
  font-weight: 600;
  text-transform: uppercase;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.position {
  border: 1px $bs $bc;
  border-radius: 3px;
  position: relative;
  min-height: 109px;
  //overflow-x: hidden;
  &:hover {
    //background: $e3;
  }
}

.position-top,
.position-bottom,
.position-header_top,
.position-footer_top,
.position-footer_bottom {
  margin-bottom: $pad2;
}

.position-header_top {
  margin-top: 5px;
  margin-bottom: $pad2 - 5px;
}

.position-column_left {
  width: $column-width;
  margin-right: $pad2;
}

.position-column_right {
  margin-left: $pad2;
  width: $column-width;
}

.position-column_left,
.position-column_right {
  > .row-wrapper {
    min-height: 108px;
    > .move-item:not(:last-child) {
      border-bottom: 0;
    }
    .position-row {
      min-height: 108px;
    }
  }
}

.default-content + .position-content_bottom,
.position-content_top + .default-content,
.position-content_top + .position-content_bottom {
  margin-top: $pad2;
}

/******************************
 Buttons Opacity
*******************************/

//.row-layouts,
.row-buttons,
.column-buttons,
  //.column-width,
.module-buttons {
  opacity: 0;
  .button + .button {
    margin-left: 0;
  }
}

/******************************
 Rows
*******************************/

$pad: 17px;

.row-heading {
  background: $f7;
  background: $e3;
  background: $alice;
  border-bottom: 1px $bs $bc;
}

.row-id {
  position: absolute;
  top: 4px;
  left: 27px;
  color: $steel;
  font-weight: 400;
  font-size: 11px;
}

.row-layouts {
  left: 50%;
  transform: translateX(-50%);
  position: absolute;
  top: 7px;
  ul {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    li {
      margin-right: 8px;
      width: 25px;
      height: 11px;
      position: relative;
      display: flex;
      align-items: center;
      cursor: pointer;
      &:hover {
        span {
          background: $hover;
        }
      }
      span {
        //transition: all 0.1s ease-out;
        background: $bor;
        display: block;
        width: 100%;
        height: 100%;
        margin-right: 1px;
        &:last-of-type {
          margin-right: 0;
        }
      }
      .row-layout-50 {
        width: 50%;
      }
      .row-layout-25 {
        width: 25%;
      }
      .row-layout-66 {
        width: 66.666666%;
      }
      .row-layout-33 {
        width: 33.333333%;
      }
    }
  }
}

.row-buttons {
  top: 0;
  height: 24px;
  right: 0;
  border-left: 1px solid $bor;
  background: white;
  padding: 0 4px;
  .button {
    display: flex;
    align-items: center;
    justify-content: center;
    top: -1px;
  }
}

.row-wrapper {
  padding-top: 0;
  background: $e7;
  min-height: 107px;
  height: 100%;

  &:empty {
    background: transparent;
    background: url("../assets/image/dots.png");
    background-position: 8px 8px;
  }

  > .move-item {
    background: $bor;
    z-index: 1;
    &:hover {
      z-index: 2;
      .row-buttons,
      .row-layouts {
        opacity: 1;
      }
    }
    &:not(:last-child) {
      border-bottom: 1px $bs $bc;
    }
  }
}

.position-row {
  display: flex;
  flex-flow: column nowrap;
  border-bottom: 1px $bs $bc;
  position: relative;
  min-height: 107px;
  &:hover {
    box-shadow: 0 1px 30px -2px rgba(0, 0, 0, 0.2);
    border-bottom: 1px $bs $bc;
  }
}

/******************************
 Columns
*******************************/

.column-wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: -2px;
  > .move-item {
    background: $e7;
    flex: 0 0 auto;
    &:hover {
      .column-buttons,
      .column-width {
        opacity: 1;
      }
    }
  }
}

.column-wrapper > .move-item {
  margin-bottom: 1px;
}

.column-buttons {
  right: 4px;
  top: 3px;
}

.position-column,
.column-wrapper {
  min-height: 82px;
  position: relative;
  height: 100%;
}

.column-wrapper:empty {
  background: $e2;
}

.column-wrapper:empty,
.module-wrapper:empty {
  &::before {
    content: "Empty Row";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-family: $mont;
    color: color($dd shade(5%));
    text-transform: uppercase;
    font-size: 18px;
    width: 100%;
    text-align: center;
  }
}

.module-wrapper:empty::before {
  content: "Empty Column";
  top: 52%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.column-wrapper > .move-item[style="width: 20%;"] .module-wrapper:empty::before,
.column-wrapper > .move-item[style="width: 25%;"] .module-wrapper:empty::before {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 15px;
  left: 53%;
  top: 54%;
}

$white: white;

@keyframes overlay {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.column-width {
  position: absolute;
  font-size: 12px;
  font-weight: 400;
  color: $steel;
  display: flex;
  align-items: center;
  left: 20px;
  top: 4px;
  padding: 0 10px 0 0;
  //z-index:1000;
  > span {
    position: relative;
    display: block;
    padding: 3px 0;
    width: 40px;
    cursor: default;
    &::after {
      //content: "\f107";
      font-size: 12px;
      right: -2px;
      top: 3px;
      position: absolute;
      font-family: icomoon, sans-serif;
    }
    &.width-100 {
      &::after {
        right: -5px;
      }
    }
  }
  > div {
    display: none;
    animation: overlay .2s ease;
    top: 62%;
    left: 0;
    z-index: 10000;
    position: absolute;
    padding-top: 10px;
  }
  &:hover {
    > div {
      display: block;
    }
  }
  ul {
    background-color: $quartz;
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    min-width: 120px;
    box-shadow: 2px 2px 8px 0 rgba(0, 0, 0, .2);
    border-radius: 3px;
    &::before {
      content: "\f0d8";
      font-size: 30px;
      left: 10px;
      top: -12px;
      color: $quartz;
      z-index: 0;
      position: absolute;
    }
    li {
      list-style: none;
      padding: 4px;
      width: 30px;
      border-width: 0 1px 0 0;
      border-color: color($quartz tint(20%));
      border-style: solid;
      color: $ee;
      cursor: pointer;
      z-index: 2;
      position: relative;
      font-size: 10px;
      text-align: center;
      &:hover {
        //color: white;
        background-color: color($quartz shade(20%));
      }
      &:nth-child(-n+16) {
        border-width: 0 1px 1px 0;
      }
      &:nth-child(4),
      &:last-child {
        border-right-width: 0;
      }
    }
  }
  .button {
    color: inherit;
    padding: 5px 5px;
    font-size: 10px;
    &:hover {
      color: $hover;
    }
  }
  &.module-width {
    left: auto;
    right: 42px;
    top: 5px;
    text-align: right;
    opacity: 0 !important;
    visibility: hidden;
    padding: 0;
    > div {
      left: 50%;
      transform: translateX(-50%);
    }
    ul::before {
      left: 50%;
      transform: translateX(-50%);
      //margin-left:-2px;
    }
  }
}

.position-column_left,
.position-column_right {
  .column-width {
    //display: none !important;
    opacity: .5 !important;
    ul {
      display: none !important;
    }
    > span {
      visibility: hidden;
      &::before {
        visibility: visible;
        font-family: $sans;
        content: "100%";
      }
      &::after {
        display: none;
      }
    }
  }
  .column-wrapper {
    margin-bottom: -1px;
  }
  .is-dragging .column-width > span::before {
    display: none;
  }
  > .row-wrapper > .move-item:last-child > .position-row:last-child {
    border-bottom-width: 0;
  }
}

.modal {
  .column-width {
    left: auto;
    top: auto;
    position: relative;
    background: white;
    border: 1px solid $dd;
    height: 29px;
    width: 75px;
    > div {
      top: 85%;
      left: 0;
    }
    &::after {
      color: white;
      content: "\f107";
      font-size: 13px;
      right: 3px;
      top: 3px;
      position: absolute;
      font-family: icomoon, sans-serif;
      background-color: #BFBFBF;
      width: 21px;
      height: 21px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    ul {
      top: 125%;
      left: 0;
      li {
        margin-top: 0;
        justify-content: center;
      }
    }
    > span {
      display: flex;
      align-items: center;
      justify-content: center;
      &::after {
        display: none;
      }
    }
    + div > ul {
      position: relative;
      z-index: 10;
      > li {
        z-index: 1;
        &:hover {
          z-index: 2;
        }
      }
    }
  }
}

/******************************
 Modules
*******************************/

$mod-bg: $f7;

.module-wrapper {
  display: flex;
  flex-grow: 1;
  flex-flow: row wrap;
  padding: 0 $pad - 2 0 $pad + 10;
  min-height: 47px;
  margin-top: 5px;
  margin-bottom: 5px;
  margin-right: -10px;
  > .move-item {
    padding-right: 10px;
    margin-bottom: 10px;
    position: relative;
    z-index: 1;
    min-width: 60px;
    width: 100%;
    &[style="width: 100%;"] {
      //padding-right:0;
    }
    &:hover {
      z-index: 2;
      .module-buttons,
      .column-width.module-width {
        opacity: 1 !important;
        visibility: visible;
      }
    }
  }
  .move-handle {
    max-height: 35px;
  }
}

.module-buttons {
  top: 0;
  right: 0;
  padding: 0 3px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(to right, rgba(247, 248, 252, 0.5) 0%, $mod-bg 20%);
  z-index: 1;
}

.position-module {
  border: 1px solid $bor;
  background: $mod-bg;
  position: relative;
  flex-grow: 1;
  border-radius: 2px;
  padding: 7px;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 35px;
  a.ui-edit {
    position: relative;
    transform: scale(1);
    background: transparent;
    box-shadow: none;
    color: $steel;
    font-size: 13px;
    cursor: pointer;
    margin: 0 3px;
    &:hover {
      color: $blue;
      background-color: transparent;
    }
  }
  //box-shadow: 1px 1px 1px rgba(0, 0, 0, .1);
  &:hover {
    .module-id {
      opacity: 1;
      &:hover {
        color: $bg;
      }
    }
  }
}

.module-id {
  opacity: .5;
  cursor: pointer;
  position: absolute;
  left: 20px;
  top: 0;
  height: 100%;
  color: $dark;
  z-index: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: 33px;
  width: calc(100% - 20px);
  > span {
    display: block;
    width: calc(100%);
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

/******************************
 Side Columns
*******************************/

.position-column_left,
.position-column_right {
  .row-layouts,
  .column-width .button,
  .module-width {
    display: none;
  }
  .module-id {
    width: calc(100% - 30px);
  }

  .column-wrapper,
  .module-wrapper {
    > .move-item {
      width: 100% !important;
    }
  }
  .row-wrapper > div:only-of-type {
    .column-wrapper > .move-item:last-child {
      border-bottom: 1px $bs $bc;
    }
  }
  .row-wrapper {
    margin-bottom: -1px;
    > .move-item {
      //border-bottom: 1px $bs $bc;
    }
    > div:last-of-type {
      .column-wrapper {
        margin-bottom: -1px;
      }
    }
  }
}

/******************************
 Modules
*******************************/

.edit-module {
  .modal-body {
    .page {
      padding-top: 0;
    }
    .j-header {
      display: none;
    }
    .j-content {
      padding-top: 0;
    }
    .tab-items {
      border-left: 0;
      height: calc(100% - 55px);
    }
  }
}

/******************************
 Header Builder
*******************************/

.page-module-header {
  .j-content {
    padding: 15px;
  }
}

/******************************
 Accordion
*******************************/

.accordion-content {
  .position {
    .new-item.button {
      padding: 5px;
      background: $quartz;
      a {
        height: 100%;
        background: $blue;
        border-radius: 3px;
        padding: 10px;
        transition: all 0.1s;
        &:hover {
          background: color($blue shade(10%));
        }
        &:active {
          box-shadow: inset 0 2px 15px 0 rgba(0, 0, 0, 0.2);
        }
      }
    }
  }
}

.accordion-group.grid-builder {
  .accordion-content {
    > div {
      padding: 10px 0 0;
      display: flex;
      flex-direction: column;
      align-items: center;
      background-color: $fa;
      border: none;
    }
  }
}

.grid-builder.popup-builder{
  .accordion-content > div + div{
    .new-item{
      margin-top: 10px;
      left: 50%;
      transform: translateX(-50%);
    }
    .row-wrapper{
      margin: 0 10px 15px 10px;
      width: auto;
    }
  }
}

.grid-builder {
  .accordion-content {
    .new-item.button {
      cursor: pointer;
      background: #238cd2;
      color: white;
      line-height: 100%;
      height: 20px;
      border-radius: 2px;
      position: relative;
      display: inline-flex;
      align-items: center;
      width: auto;
      padding: 0;
      &:hover a span {
        background-color: $green;
      }
      a {
        font-size: 10px;
        color: white;
        text-transform: uppercase;
        padding: 4px 25px 3px 5px;
        font-weight: 600;
        span {
          position: relative;
          display: inline-flex;
          align-items: center;
          justify-content: center;
          background-color: #1A738E;
          position: absolute;
          width: 20px;
          height: 20px;
          right: 0;
          top: 0;
          font-size: 17px;
          visibility: visible;
          &::before {
            display: none;
          }
        }
      }
    }
    .row-wrapper {
      width: 100%;
      position: relative;
      border: 1px solid rgb(198, 200, 207);
      margin-top: 0;
      &:empty::before {
        content: "Click Add Row to begin";
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-family: "Montserrat", "Open Sans", sans-serif;
        color: rgb(209, 212, 215);
        text-transform: uppercase;
        font-size: 18px;
        width: 100%;
        text-align: center;
      }
    }
  }
}

.page-layout-new .global-modules {
  display: none;
}

.has-status-off{
  .column-wrapper{
    opacity: .5;
    pointer-events: none;
  }
  .row-heading{
    background: #F2E3E2;
  }
}

.position-row{
  //phone only
  &.has-status-off-tablet.has-status-off-desktop{
    .row-heading{
      background: #d4efdf;
    }
  }
  //phone and tablet
  &.has-status-off-desktop{
    .row-heading{
      background: #f1eec2;
    }
  }
  //desktop only
  &.has-status-off-tablet.has-status-off-phone{
    .row-heading{
      background: #bae3fb;
    }
  }
}
